import React from 'react';
import { Parallax, ParallaxLayer } from '@react-spring/parallax';

import '../styles/parallax.scss'; // Import the CSS file

function MyComponent() {
  return (
    <div className="parallax-container">
      <Parallax pages={3}>
        <ParallaxLayer offset={0} speed={0.5}>
          <div className="parallax-layer" style={{ backgroundColor: 'blue' }}>
            First Parallax Layer
          </div>
        </ParallaxLayer>

        <ParallaxLayer offset={1} speed={1}>
          <div className="parallax-layer" style={{ backgroundColor: 'green' }}>
            Second Parallax Layer
          </div>
        </ParallaxLayer>

        <ParallaxLayer offset={2} speed={1.5}>
          <div className="parallax-layer" style={{ backgroundColor: 'orange' }}>
            Third Parallax Layer
          </div>
        </ParallaxLayer>
      </Parallax>
    </div>
  );
}

export default MyComponent;
